<script>
  import { onMount } from "svelte";
  import { json } from "d3-fetch";

  import "./../css/app.css";
  // import {} from "./../stores";

  import YesterdayTimeline from "./YesterdayTimeline.svelte";

  let data = {};
  let didError = false;
  const baseUrl = "https://pudding.cool/misc/analytics";
  const cuts = [
    "browser-monthly",
    "device-monthly",
    "device-pages-alltime",
    "pages-hourly-yesterday",
    "referrers-domain-alltime",
    "referrers-topic-alltime"
  ];

  onMount(() => {
    try {
      cuts.forEach(async cut => {
        const res = await json(
          `${baseUrl}/${cut}.json?version=${new Date().getTime()}`
        );
        data[cut] = res["data"];
        console.log(data);
      });
    } catch (e) {
      console.log(e);
      didError = true;
    }
  });
</script>

<div class="c">
  {#if didError}
    <div class="error">There was an issue loading the data</div>
  {:else if data}
    <YesterdayTimeline data="{data['pages-hourly-yesterday']}" />
  {/if}

</div>

<style>
  .c {
    padding: 3em 2em;
  }
  .error {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 90vh;
  }
</style>
