<script>
  import { range, sum } from "d3-array";
  import { scaleLinear } from "d3-scale";
  import { format } from "d3-format";
  import { timeFormat, utcParse } from "d3-time-format";
  import { area, curveMonotoneX } from "d3-shape";

  export let data = [];
  console.log(data);

  const numberToHour = d => timeFormat("%-I %p")(utcParse("%-H")(d));

  let parsedData = [];
  let areaD = "";
  let xTicks = [];
  let yTicks = [];
  let selectedItemIndex = 0;

  const hours = range(0, 24);

  const updateData = () => {
    if (!data || !data.length) return;

    let totalTraffic = hours.map(_ => 0);
    const values = Object.values(data).map(d => d["values"]);
    values.forEach(hours => {
      hours.forEach(({ hour, count }) => {
        totalTraffic[hour] += count;
      });
    });
    console.log(totalTraffic);

    const xScale = scaleLinear()
      .domain([0, 23])
      .range([0, 100]);
    const yScale = scaleLinear()
      .domain([0, Math.max(...totalTraffic)])
      .range([100, 0]);
    const areaGenerator = area()
      .x((d, i) => xScale(i))
      .y1(d => yScale(d))
      .y0(yScale(0))
      .curve(curveMonotoneX);
    areaD = areaGenerator(totalTraffic);
    xTicks = xScale.ticks(5).map(value => [numberToHour(value), xScale(value)]);
    yTicks = yScale
      .ticks(5)
      .map(value => [value, yScale(value)])
      .filter(d => d[0]);

    parsedData = data
      .map(d => ({
        ...d,
        totalCount: sum(d["values"].map(d => d["count"])),
        area: areaGenerator(
          hours.map(
            i => (d["values"].find(d => d["hour"] == i) || {})["count"] || 0
          )
        )
      }))
      .sort((a, b) => b["totalCount"] - a["totalCount"]);
  };
  $: data, updateData();
</script>

<div class="c">
  <div class="timeline">
    <svg viewBox="0 0 100 100" preserveAspectRatio="none">
      <path class="total" d="{areaD}"></path>
      <path
        class="highlighted"
        d="{parsedData[selectedItemIndex] ? parsedData[selectedItemIndex]['area'] : ''}"></path>
    </svg>
    <div class="ticks ticks--x">
      {#each xTicks as [label, offset]}
        <div class="tick tick--x" style="{`left: ${offset}%`}">{label}</div>
      {/each}
    </div>
    <div class="ticks ticks--y">
      {#each yTicks as [label, offset]}
        <div class="tick tick--y" style="{`top: ${offset}%`}">{label}</div>
      {/each}
    </div>
  </div>
  <div class="list">
    <div class="header">
      <div>Page</div>
      <div>Visits</div>
    </div>
    <div class="items">
      {#each parsedData as page, i}
        <div
          class="item"
          class:active="{selectedItemIndex == i}"
          on:mouseenter="{() => (selectedItemIndex = i)}"
          on:mouseleave="{() => (selectedItemIndex = null)}">
          <div class="label" title="{page.hed}">{page.hed}</div>
          <div class="value">{format(',')(page.totalCount)}</div>
        </div>
      {/each}
    </div>
  </div>
</div>

<style>
