    display: flex;
    width: 100%;
    height: 15em;
    margin: 0 auto;
  }
  .timeline {
    position: relative;
    flex: 2;
  }
  svg {
    width: 100%;
    height: 100%;
  }
  .list {
    flex: 1;
    font-size: 0.8em;
