  }
  .header {
    display: flex;
    justify-content: space-between;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    font-size: 0.9em;
    font-family: var(--figure-font);
    margin: 0 1em;
  }
  .items {
    height: 19em;
    overflow: hidden;
  }
  .items:hover {
    overflow: auto;
  }
  .total {
    fill: var(--gray-light);
  }
  .highlighted {
    fill: var(--accent-medium);
    transition: all 0.3s ease-out;
  }
  .item {
    display: flex;
    justify-content: space-between;
    padding: 0.3em 1em;
  }
  .item.active {
    background: var(--accent-lighter);
  }
  .label {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    padding-right: 0.6em;
  }
  .value {
    font-family: var(--figure-font);
    font-feature-settings: "tnum", 1;
  }

  .tick {
    position: absolute;
    top: 0;
    left: 0;
  }
  .ticks {
    position: absolute;
